@import url(https://fonts.googleapis.com/css2?family=Pacifico&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Pacifico&display=swap);
body {
  overflow-y: hidden;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  margin: 0;
}
.App{
  height: 100vh;
  overflow: hidden;
}

body {
  --com-color: #003acf;
}

.header {
	overflow: hidden;
	width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
	padding: 10px 0;
	position: fixed;
    background-color:  #003acf;
    color: white;
}
.header__left {
	font-family: 'Pacifico', cursive;
	flex: 0.3 1;
	display: flex;
	align-items: center;
	margin-left: 20px;
}
.header__left > h1 {
	font-size: 25px;
}

.cmlogo {
	padding-right: 15px;
	height: 30px;
	object-fit: contain;
}

.header__right{
	flex:0.3 1;
	display: flex;
	align-items: flex-end;
}

.header__avatar {
	margin-left: auto;
	margin-right: 20px;
}
.sidebar {
	margin-top: 64px;;
	position: fixed;
	height: 700px;
	background-color:  #003acf;
	border-top: 1px solid #110038;
	max-width: 180px;
	color: white;
	overflow-y: auto;
}

.sidebar::-webkit-scrollbar{
	width: 1em;
}
.sidebar__header {
	
display: flex;
align-items: center;
border-bottom: 1px solid #110038;
padding: 13px;
}
.sidebar__header > h3{
	margin-right: 2px;
}

.sidebar__info {
	flex: 1 1;
}

.sidebar__info > h2 {
	font-size: 15px;
	font-weight: 900;
	margin-bottom: 5px;
}

.sidebar__info > h3 {
	display: flex;
	align-items: center;
	font-size: 13px;
	font-weight: 400;
}

.sidebar__info > h3 > .MuiSvgIcon-root {
	font-size: 14px;
	margin-top: 1px;
	margin-right: 2px;
	color: green;
}

.sidebar__header > .MuiSvgIcon-root{
	padding: 8px;
	color: #49274b;
	font-size: 18px;
	background-color: white;
	border-radius: 999px;
}

.add {
	background-color: #ffa826;
	color: white;
}
.sidebarOption {
	display: flex;
	align-items: center;
	font-size: 12px;
	padding-left: 2px;
	cursor: pointer;
}

.sidebarOption:hover {
	opacity: 0.9;
	background-color: #1b2930;
}

.sidebarOption__icon {
	padding: 10px;
	font-size: 15px !important;
}
.sidebarOption__channel {
	padding: 10px 0;
}
.sidebarOption__hash {
	padding: 10px;
}
.sidebarOption > h3 {
	font-weight: 500;
}
.chat{
	overflow-y: scroll;
	height: 460px;
	flex: 0.7 1;
	flex-grow: 1;
	padding-bottom: 150px;
	padding-top: 60px;
	margin-left: 180px;
	width: 90%;
}
.detail{
	padding-right: 80px;
}
.chat__header {
	margin-top: 4px;
	width: 90%;
	background-color: white;
	position: fixed;
	display: flex;
	align-items: center;
	justify-content: space-between;
	padding: 20px;
	border-bottom: 1px solid lightgray;
}

.chat__channelName {
	display: flex;
	text-transform: lowercase;
}

.chat__channelName > .MuiSvgIcon-root {
	margin-left: 10px;
	font-size: 18px;
}

.chat__headerRight > p {
	display: flex;
	align-items: center;
	font-size: 14px;
}
.chat__headerRight > p > .MuiSvgIcon-root {
	margin-right: 5px !important;
	font-size: 16px;
}
.chat__messages{
	padding-top: 60px;
}
.message{
	background-color: yellow;
	display: flex;
	align-items: center;
	padding: 20px;
}

.message > img {
	border-radius: 50%;
	height: 50px;
	width: 50px;
	object-fit: contain;
	padding-left: 0;
	padding: 5px;
}
.message__timestamp {
	color: gray;
	font-weight: 300;
	font-size: 13px;
}
.ChatInput {
	width: 100%;
	border-radius: 20px;
	background-color: green;
}

.chat__message {
	position: relative;
	display: flex;
	justify-content: center;
}

.input__field {
	position: fixed;
	bottom: 30px;
	width: 60%;
	border: 1px solid gray;
	border-radius: 3px;
	padding: 20px;
}
.btn {
	display: none !important;
}
.google__signin {
	cursor: pointer;
	display: flex;
	height: 35px;
	background-color: white;
	align-items: center;
	padding-right: 10px;
	padding-top: 5px;
	padding-bottom: 5px; 
	border-radius: 20px;
	box-shadow: 0 1px 3px rgba(0, 0, 0, 12);
}
.google__signin > img {
	height: 30px;
	object-fit: contain;;
	margin-left: 50px;
}

.google__signin > h1 {
	margin-left: 50px;
	font-size: 20px;
	color: darkgray;
}

.login {
	height: 100vh;
	background-color: var(--com-color);
	display: grid;
	place-items: center;
}

.login__container {
	padding: 50px;
	text-align: center;
	background-color: white;
	border-radius: 10px;
	box-shadow: 0 1px 3px rgba(0, 0, 0, 12);}

.login__container > img {
	object-fit: contain;
	height: 100px;
}
.login__container > h1 {
	font-family: 'Pacifico', cursive;
	color: #001038;
	margin-bottom: 50px;
}
.login__container > button {
	margin-top:  50px;
	text-transform: inherit !important;
	background-color: #0a8d48 !important;
	color: white;
}
